.resume--header {
	width: 100%;
	height: 100vh;
	padding: 60px 0 80px 0;
	background-color: #ab4f4f;
	color: #fff;
}

.resume--header--h1 {
	margin-top: 0;
	font-size: 2.5em;
	padding-bottom: 40px;
}

.resume--header--wrapper {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.resume--header--languages {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	text-align: center;
	width: 100%;
	max-width: 500px;
	padding: 0 20px;
	border: 1px solid #fff;
	border-radius: 3px;
}

.resume--header--languages h1 {
	margin-bottom: 0;
}

.resume--header--languages--wrapper {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	height: 100%;
}

@media (max-width: 875px) {
	.resume--header--wrapper {
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-align: center;
		    align-items: center;
	}

	.resume--header--languages {
		-ms-flex-order: 2;
		    order: 2;
	}
}

@media (max-width: 875px) {
	.resume--header--languages {
		margin-top: 40px;
		padding-bottom: 40px;
	}
	.resume--header {
		height: 100%;
	}
}

@media (max-width: 585px) {
	.resume--header--languages {
		-ms-flex-align: center;
		    align-items: center;
		width: 80%;
	}
}

@media (max-width: 485px) {
	.resume--header--languages {
		padding-bottom: 40px;
	}
}



